import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import Cookie from "js-cookie";
import Pubsub from "pubsub-js";

export default class Header extends Component {
  state = {
    auth: "",
  };
  componentDidMount() {
    var that = this;
    Pubsub.subscribe("auth", function (msg, data) {
      console.log(msg.data);
      that.setState({
        auth: data,
      });
    });
  }

  render() {
    if (Cookie.get("auth")) {
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">
                conduit
              </a>
              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                  <NavLink activeClassName="active" className="nav-link" to="/">
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    activeClassName="active"
                    className="nav-link"
                    to="/editor"
                  >
                    <i className="ion-compose"></i>&nbsp;New Post
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    activeClassName="active"
                    className="nav-link"
                    to="/settings"
                  >
                    <i className="ion-gear-a"></i>&nbsp;Settings
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link ng-binding active"
                    ui-sref-active="active"
                    ui-sref="app.profile.main({ username: $ctrl.currentUser.username })"
                    to="/profile"
                    activeClassName="active"
                  >
                    <img
                      className="user-pic"
                      ng-src="https://z3.ax1x.com/2021/04/29/gFXnCF.png"
                      src="https://z3.ax1x.com/2021/04/29/gFXnCF.png"
                    />
                    jason_1983
                  </NavLink>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      );
    } else {
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">
                conduit
              </a>
              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                  <NavLink activeClassName="active" className="nav-link" to="/">
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    activeClassName="active"
                    className="nav-link"
                    to="/register"
                  >
                    Sign up
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    activeClassName="active"
                    className="nav-link"
                    to="/login"
                  >
                    Sign in
                  </NavLink>
                </li>
              </ul>
            </div>
          </nav>
          ;
        </div>
      );
    }
  }
}
