import Cookies from "js-cookie";
import React, { Component } from "react";
import Pubsub from "pubsub-js";
import { request } from "../api/api";

class A extends React.Component {
  blur = (event) => {
    var value = event.target.value;
    console.log(value);
    Pubsub.publish("value", value);
  };
  render() {
    return (
      <div>
        <fieldset className="form-group">
          <input
            className="form-control form-control-lg"
            type="text"
            placeholder="Your Name"
            onBlur={this.blur}
          />
        </fieldset>
      </div>
    );
  }
}

export default class Login extends Component {
  state = {
    username: "",
    email: "",
    password: "",
  };
  componentDidMount() {
    Pubsub.subscribe("value", (msg, data) => {
      console.log(msg, data);
      this.setState({
        username: data,
      });
    });
  }

  onsubmit = (event) => {
    event.preventDefault();

    let user = {
      email: this.state.email,
      password: this.state.password,
    };
    request({
      method: "POST",
      url: "/api/users/login",
      data: {
        user,
      },
    }).then((res) => {
      console.log("token", res.data.user);
      console.log("fanhui", res);
      console.log("props", this.props);
      Cookies.set("auth", res.data.user.token);
      this.props.history.push("/");
      //发布
      Pubsub.publish("auth", res.data.user.token);
    });
  };
  onbluremail = (event) => {
    this.setState({
      email: event.target.value,
    });
  };
  onblurpad = (event) => {
    this.setState({
      password: event.target.value,
    });
  };

  isLogin() {
    return this.props.match.path === "/login";
  }
  render() {
    return (
      <div className="auth-page">
        <div className="container page">
          <div className="row">
            <div className="col-md-6 offset-md-3 col-xs-12">
              <h1 className="text-xs-center">
                {this.isLogin() ? "Sign in" : "Sign up"}
              </h1>
              <p className="text-xs-center">
                <a href="/#">
                  {this.isLogin() ? "Need an account?" : "Have an account"}
                </a>
              </p>

              <ul className="error-messages">
                <li>That email is already taken</li>
              </ul>

              <form>
                {this.isLogin() ? "" : <A />}
                <fieldset className="form-group">
                  <input
                    className="form-control form-control-lg"
                    type="text"
                    placeholder="Email"
                    onBlur={this.onbluremail}
                  />
                </fieldset>
                <fieldset className="form-group">
                  <input
                    className="form-control form-control-lg"
                    type="password"
                    placeholder="Password"
                    onBlur={this.onblurpad}
                  />
                </fieldset>
                <button
                  className="btn btn-lg btn-primary pull-xs-right"
                  onClick={this.onsubmit}
                >
                  {this.isLogin() ? "Sign in" : "Sign up"}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
