import React, { Component } from "react";
import { request } from "../api/api";
import { NavLink } from "react-router-dom";
import { deleFavorite, addFavorite } from "../api/favorite.js";

export default class Home extends Component {
  state = {
    articles: [],
    tags: [],
    pages: "", //页码
    num: "",
    //数据在加载过程中点赞禁止
    favoritedisabled: false,
  };

  fnFavorit = async (data) => {
    this.setState({
      favoritedisabled: true,
    });
    console.log("fnFavorit", data);
    if (data.favorited) {
      //取消点赞
      await deleFavorite(data).then((res) => {
        console.log("取消", res);
      });
      data.favorited = false;
      data.favoritesCount -= 1;

      let key = this.state.articles.indexOf(data);
      this.state.articles[key] = data;
      await this.setState({
        articles: this.state.articles,
      });
    } else {
      //点赞
      await addFavorite(data).then((res) => {
        console.log("添加", res);
      });
      data.favorited = true;
      data.favoritesCount += 1;

      let key = this.state.articles.indexOf(data);
      this.state.articles[key] = data;
      await this.setState({
        articles: this.state.articles,
      });
    }
  };
  click = (index) => {
    console.log(index);
    this.setState(
      {
        num: index,
      },
      () => {
        request({
          method: "get",
          url: "/api/articles?limit=10&offset=" + this.state.num * 10,
          pramas: {},
        }).then((res) => {
          // console.log("articles", res);
          this.setState({
            articles: res.data.articles,
          });
        });
      }
    );
  };
  componentDidMount() {
    request({
      method: "GET",
      url: "/api/articles",
      params: {
        limit: 10,
        offset: this.state.num * 10,
      },
    }).then((res) => {
      console.log("articles", res);

      this.setState({
        articles: res.data.articles,
        pages: res.data.articlesCount / res.data.articles.length,
      });
    });
    request({
      method: "get",
      url: "/api/tags",
      params: {},
    }).then((res) => {
      console.log("tags", res);
      this.setState({
        tags: res.data.tags,
      });
    });
  }

  render() {
    console.log("page", this.state.pages);
    return (
      <div className="home-page">
        <div className="banner">
          <div className="container">
            <h1 className="logo-font">conduit</h1>
            <p>A place to share your knowledge.</p>
          </div>
        </div>

        <div className="container page">
          <div className="row">
            <div className="col-md-9">
              <div className="feed-toggle">
                <ul className="nav nav-pills outline-active">
                  <li className="nav-item">
                    <a className="nav-link disabled" href="/#">
                      Your Feed
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link active" href="/#">
                      Global Feed
                    </a>
                  </li>
                </ul>
              </div>

              {this.state.articles.map((element, index) => {
                return (
                  <div className="article-preview" key={index}>
                    <div className="article-meta">
                      <a href="profile.html">
                        <img src={element.author.image} />
                      </a>
                      <div className="info">
                        <a href="/#" className="author">
                          {element.author.username}
                        </a>
                        <span className="date">{element.createdAt}</span>
                      </div>
                      <button
                        onClick={() => {
                          this.fnFavorit(element);
                        }}
                        className="btn btn-outline-primary btn-sm pull-xs-right"
                      >
                        <i className="ion-heart"></i> {element.favoritesCount}
                      </button>
                    </div>
                    <a href="/#" className="preview-link">
                      <h1>{element.title}</h1>
                      <p>{element.description}</p>
                      <span>Read more...</span>
                    </a>
                  </div>
                );
              })}

              <list-pagination
                total-pages="$ctrl.listConfig.totalPages"
                current-page="$ctrl.listConfig.currentPage"
                ng-hide="$ctrl.listConfig.totalPages <= 1"
                className="ng-isolate-scope"
              >
                <nav>
                  <ul className="pagination">
                    {new Array(this.state.pages).fill("").map((item, index) => {
                      return (
                        <li
                          key={item + index}
                          className={
                            this.state.num == index
                              ? "page-item active"
                              : "page-item"
                          }
                        >
                          <NavLink
                            onClick={() => this.click(index)}
                            className="page-link ng-binding"
                            to=""
                          >
                            {index + 1}
                          </NavLink>
                        </li>
                      );
                    })}
                  </ul>
                </nav>
              </list-pagination>
            </div>

            <div className="col-md-3">
              <div className="sidebar">
                <p>Popular Tags</p>

                <div className="tag-list">
                  {this.state.tags.map((item, index) => {
                    return (
                      <a key={index} href="/#" className="tag-pill tag-default">
                        {item}
                      </a>
                    );
                  })}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
