import React, { Component } from "react";
import Footer from "./Footer";
import Header from "./Header";
import Home from "./Home";
import Login from "./Login";
import Article from "./Article";
import Setting from "./Setting";
import Profile from "./Profile";
import "./main.css";
import Cookie from "js-cookie";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

export default class App extends Component {
  render() {
    if (Cookie.get("auth")) {
      return (
        <div>
          <Router>
            <Header></Header>
            <Switch>
              <Route exact path="/">
                <Home></Home>
              </Route>
              <Route exact path="/editor" component={Article}></Route>
              <Route exact path="/settings" component={Setting}></Route>
              <Route exact path="/login" component={Login}></Route>
              <Route exact path="/register" component={Login}></Route>
              <Route exact path="/profile" component={Profile}></Route>
            </Switch>
            <Footer></Footer>
          </Router>
        </div>
      );
    } else {
      return (
        <div>
          <Router>
            <Header></Header>
            <Switch>
              <Route exact path="/">
                <Home></Home>
              </Route>
              <Route exact path="/editor" component={Home}></Route>
              <Route exact path="/settings" component={Home}></Route>
              <Route exact path="/login" component={Login}></Route>
              <Route exact path="/register" component={Login}></Route>
              <Route exact path="/profile" component={Home}></Route>
            </Switch>
            <Footer></Footer>
          </Router>
        </div>
      );
    }
  }
}
